@import "./core/Variables";
@import "./core/Reset";

.form {
	margin: 10px;
	display: flex;
	justify-content: center;
	font-size: 12px;
	gap: 20px;
	font-family: $font-second;
	color: $color-golden;
	@media all and (min-width: 768px) {
		margin: 40px;

		font-size: 25px;
		gap: 30px;
	}
	@media all and (min-width: 1200px) {
		margin-top: 100px;

		
	}
}

.background-main {
	margin-left: 15%;
	margin-right: 15%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.mainhoroscope {
	margin-top: 40px;
	display: grid;
	gap: 30px;
	grid-template-columns: 1fr 1fr 1fr;
	margin: 60px 300px 100px 300px;

	@media all and (min-width: 768px) {
		gap: 30px;
		grid-template-columns: 1fr 1fr 1fr 1fr ;
			margin: 30px 300px 100px 300px;
	}

	@media all and (min-width: 1200px) {
		gap: 30px;
		grid-template-columns: 1fr 1fr 1fr 1fr  1fr 1fr ;
			margin: 110px 300px 100px 300px;
	}
}

.onehoroscope {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: $color-third;

	&__name {
		font-size: 12px;
		color: inherit;
		text-transform: uppercase;
		text-align: center;
		filter: inherit;
		@media all and (min-width: 768px) {
			font-size: 22px;
		}
	}

	&__img {
		width: 100px;
		height: 100px;
		@media all and (min-width: 768px) {
			width: 120px;
			height: 120px;
		}
	}

	&:hover {
		transform: scale(1.1, 1.1);
		transition: transform 2s;
		filter: drop-shadow(0px 0px 6px $color-golden);
	}
}
